import { useEffect, useMemo, useRef, useState } from "react";
import { Link } from "react-router";
import { savePendingRequest, showNotification, subscribeUser } from "../../utils";
import { useBarcodeScanner } from "../../utils/barcodeDetection";
import { getCityName } from "../../utils/geolocation";
import { registerBackgroundSync } from '../../utils/backgroundFetchApi';
import { requestWakeLock } from "../../utils/wakeLock";
import './style.css';

const BASE_URL = 'https://onfire22.github.io/pwa';

const Users = () => {
  const [users, setUsers] = useState([]);
  const { codes, startBarcodeScanner } = useBarcodeScanner();
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const [showButton, setShowButton] = useState(false);
  const [location, setLocation] = useState({});
  const [city, setCity] = useState('');
  const [isVideoShown, setIsVideoShown] = useState(false);
  const [username, setUsername] = useState(null);
  const [text, setText] = useState('');

  const camRef = useRef(null);
  const webSocket = useMemo(() => {
    return new WebSocket('wss://server-zjg0.onrender.com');
  }, [username]);
  
  useEffect(() => {
    if (username) {
      subscribeUser(username);
      webSocket.onopen = () => {
        webSocket.send(JSON.stringify({ type: 'register', username }))
      }
    }
    
    webSocket.onmessage = (event) => {
      const { from, content } = JSON.parse(event.data);
      showNotification(`   ${from}`, content, `${BASE_URL}/icons/message.svg`);
    };
    return (() => {
      webSocket.close();
    });
  }, [username, webSocket]);
  
  const sendMessage = (e, message) => {
    e.preventDefault();
    const to = username === 'User1' ? 'User2' : 'User1';
    if (webSocket) {
      webSocket.send(JSON.stringify({ type: 'message', to, content: message }));
      setText('');
    } else {
      console.log(' ');
    }
  };

  const getUsers = async () => {
    if (navigator.onLine) {
      try {
        const response = await fetch('https://jsonplaceholder.typicode.com/users');
        showNotification('', '  ', `${BASE_URL}/icons/mail.svg`);
        const data = await response.json();
        setUsers(data);
      } catch (e) {
        showNotification('', '  ,  ', `${BASE_URL}/icons/error.svg`)
        console.log(e);
      }
    } else {
      await savePendingRequest({ url: 'https://jsonplaceholder.typicode.com/users', method: 'GET' })
      const swReg = await navigator.serviceWorker.ready;
      await swReg.sync.register('sync-data');
    }
  };

  useEffect(() => {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.addEventListener('message', (event) => {
        if (event.data && event.data.type === 'SYNC_COMPLETE') {
          setUsers(event.data.data[0]);
        }
      });
    }
  }, []);

  useEffect(() => {
    registerBackgroundSync();
    requestWakeLock();
  }, []);

  useEffect(() => {
    if (isVideoShown && camRef?.current) {
      startBarcodeScanner(camRef.current, setIsVideoShown);
    }
  }, [isVideoShown, startBarcodeScanner]);

  useEffect(() => {
    const handleBeforeInstallPrompt = (e) => {
      e.preventDefault(); 
      setDeferredPrompt(e);
      setShowButton(true);
    };

    window.addEventListener("beforeinstallprompt", handleBeforeInstallPrompt);

    return () => {
      window.removeEventListener("beforeinstallprompt", handleBeforeInstallPrompt);
    };
  }, []);

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation(position);
          getCityName(position.coords.latitude, position.coords.longitude)
            .then(data => setCity(data.display_name));
      },
      (error) => error,
    )}
  }, []);

  const handleInstallClick = async () => {
    if (deferredPrompt) {
      deferredPrompt.prompt();
      const { outcome } = await deferredPrompt.userChoice;
      console.log(` : ${outcome}`);
      setDeferredPrompt(null);
      setShowButton(false);
    }
  };

  const handleCloseVideo = () => {
    setIsVideoShown(false);
    camRef.current.pause();
    camRef.current.remove();
  };

  return (
    <div className="page">
      {isVideoShown && 
        <div className="camera">
          <video className="video" ref={camRef} />
          <button className="video_close" onClick={handleCloseVideo}>X</button>
        </div>
      }
      <header className="header">
        <ul className="header__menu">
          <li className="header__menu-item">
            <Link className="header__menu-button" to='/profile'> </Link>
          </li>
          <li className="header__menu-item">
            <p> :</p>
            <button
              className={username === 'User1' && 'btn-active'}
              type="button"
              onClick={() => setUsername('User1')}
            >
              User_1
            </button>
            <button
              className={username === 'User2' && 'btn-active'}
              type="button"
              onClick={() => setUsername('User2')}
            >
              User_2
            </button>
          </li>
          <li className="header__menu-item">
            {showButton && <button className="header__menu-button" onClick={handleInstallClick}></button>}
          </li>
          <li className="header__menu-item">
            <button className="header__menu-button" type="button" onClick={() => setIsVideoShown(true)}> qr </button>
          </li>
          <li className="header__menu-item">
            <button className="header__menu-button" type="button" onClick={getUsers}> </button>
          </li>
        </ul>
      </header>
      <div className="qr_codes">
        <h3> QR-:</h3>
        {codes.length > 0 ? (
          codes.map((code, index) => <p className="qr_code" key={index}>{code.rawValue}</p>)
        ) : (
          <p> </p>
        )}
      </div>
      <div className="location">
        <h3> :</h3>
        <div>: {location?.coords?.latitude || '-'}</div>
        <div>: {location?.coords?.longitude || '-'}</div>
        <div>: {city}</div>
      </div>
      <div className="message">
        <form className="form" onSubmit={(e) => sendMessage(e, text)}>
          <textarea
            className="text"
            name=""
            id=""
            cols="30"
            rows="10"
            onChange={(e) => setText(e.target.value)}
            value={text}
            placeholder={`    . ${username ? `    ${username === 'User1' ? 'User2' : 'User1'}` : ''}`}
          />
          <button type="submit" disabled={!Boolean(username)}></button>
        </form>
      </div>
      <div className="container">
        {users.map(({ id, name, phone, username, email }) => {
          return (
            <div className="user" key={id}>
              <p className="user_info">: {name}</p>
              <p className="user_info">: {phone}</p>
              <p className="user_info">: {username}</p>
              <p className="user_info">: {email}</p>
            </div>
          )
        })}
      </div>
    </div>
  );
};

export default Users;
