import { useState } from "react";
import { showNotification } from "../../utils";
import './style.css';
import {useNavigate} from "react-router";

const BASE_URL = 'https://onfire22.github.io/pwa';

const Profile = () => {
  const navigate = useNavigate();
  const [userInfo, setUserInfo] = useState({
    name: 'admin',
    organization: 't1',
  });

  const handleGoBack = () => {
    navigate('/');
  }

  const [mode, setMode] = useState('read');

  const [userInput, setUserInput] = useState({
    name: '',
    organization: '',
  });

  const handleChange = (e) => {
    setUserInput({
      ...userInput,
      [e.target.name]: e.target.value,
    })
  };

  return (
    <div>
      <button className="btn" onClick={handleGoBack} type="button">
         
      </button>
      <div className="profile">
        <div className="container">
          {mode === 'read' ? (
            <div className="info">
              <p className="profile_name"> : <b>{userInfo.name}</b></p>
              <p className="profile_organization"> : <b>{userInfo.organization}</b></p>
              <button type="button" onClick={() => setMode('edit')}></button>
            </div>
          ) : (
            <form className="form info">
              <input
                className="input"
                type="text"
                placeholder=" "
                name="name"
                value={userInput.name}
                onChange={(e) => handleChange(e)}
              />
              <input
                className="input"
                type="text"
                placeholder=" "
                name="organization"
                value={userInput.organization}
                onChange={(e) => handleChange(e)}
              />
              <button type="button" onClick={() => {
                setUserInfo(userInput);
                setMode('read');
                showNotification('', '  ', `${BASE_URL}/icons/check.svg`);
              }}></button>
            </form>
          )}
        </div>
      </div>
    </div>
  );
};

export default Profile;
